import React, { Component } from 'react';
import { Col, Row, Form } from 'antd';
import { getTrainTypes } from 'services/trainType';
import { getResgisterSessionSelect } from 'services/registerSession';
import { getFacultyByRegisterSession } from 'services/gradePartGrading';
import { getSubjectByFacultyAndSession, getClassCredit } from 'services/classCredit';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';

import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'formCreditClass' })
class CreditClass extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      registerSessionId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Đợt!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Môn học',
          },
        ],
      },
      cCSId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Lớp tín chỉ',
          },
        ],
      },
    };
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const registerSessionId = `${getFieldValue('registerSessionId')}`;
    const facultyId = getFieldValue('facultyId');
    const subjectId = getFieldValue('subjectId');
    return (
      <Form layout="vertical" onSubmit={this.handleSearch} className="form-inline-label">
        <Row gutter={24}>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Hệ">
              {getFieldDecorator(
                'trainTypeId',
                optionFieldDecorator.trainTypeId
              )(
                <SelectAjax
                  service={getTrainTypes}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Đợt">
              {getFieldDecorator(
                'registerSessionId',
                optionFieldDecorator.registerSessionId
              )(
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getResgisterSessionSelect}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Khoa">
              {getFieldDecorator(
                'facultyId',
                optionFieldDecorator.facultyId
              )(
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getFacultyByRegisterSession}
                  params={{ registerSessionId }}
                  allowGetData={!!registerSessionId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Môn học">
              {getFieldDecorator(
                'subjectId',
                optionFieldDecorator.subjectId
              )(
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getSubjectByFacultyAndSession}
                  params={{ registerSessionId, facultyId }}
                  allowGetData={!!registerSessionId && !!facultyId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item {...formItemLayout} label="Lớp">
              {getFieldDecorator(
                'cCSId',
                optionFieldDecorator.cCSId
              )(
                <SelectAjax
                  keyValue="id"
                  keyLabel="classLabel"
                  setFieldsValue={setFieldsValue}
                  service={getClassCredit}
                  params={{ registerSessionId, facultyId, subjectId }}
                  allowGetData={!!registerSessionId && !!facultyId && !!subjectId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4} style={{ textAlign: 'left' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default CreditClass;



