import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getClassCreditEnrollment = ({
  cCSId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment',
    method: 'GET',
    params: {
      cCSId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {}
  });

export const createClassCreditEnrollment = value =>
  request({
    prefix,
    url: `/ClassCreditEnrollment`,
    data: value,
    method: 'POST',
  });

export const updateClassCreditEnrollment = ({
  ccsId,
  isLocked
}) => {
  return request({
    prefix,
    url: `/ClassCreditEnrollment`,
    data: {
      ccsId,
      isLocked
    },
    method: 'PUT',
  });
};

export const exportExcel = (ccsId) =>
  requestDownload({
    prefix,
    url: `/ClassCreditEnrollment/ExportExcelCCEGradeByCCSId `,
    data: {
      ccsId
    },
    method: 'POST',
  });

export const exportPDF = (ccsId) =>
  requestDownload({
    prefix,
    url: `/ClassCreditEnrollment/ExportCCEGradeByCCSId `,
    data: {
      ccsId
    },
    method: 'POST',
  });


