import React, { Component } from 'react';
import { Card, Row, Col, Spin, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnLock, BtnExport, BtnExportPdf } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import {
  getClassCreditEnrollment,
  createClassCreditEnrollment,
  updateClassCreditEnrollment,
  exportExcel,
  exportPDF,
} from 'services/classCreditEnrollment';
import { getFormTableData, convertTableDataObjToArray } from 'utils/formUtil';

import CreditClassTable from './CreditClassTable';
import CreditClass from './CreditClass';

class InsertScorePage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataScore: null,
      rowKey: "studentId",
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getTableData = (values) => {
    const formDataObj = getFormTableData(values);
    const formDataArray = convertTableDataObjToArray(formDataObj);
    return formDataArray;
  }


  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.registerSessionId &&
        !!paramSearch.facultyId &&
        !!paramSearch.subjectId &&
        !!paramSearch.cCSId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getClassCreditEnrollment(paramSearch);
        const tempData = response.data.items.map(item => {
          if (item.gradeParts && item.gradeParts.length) {
            item.gradeParts.forEach(gradePart => {
              item[gradePart.gradePartId] = gradePart.grade;
            });
          }
          return item;
        });

        this.setState({
          dataScore: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }

  };

  handleSave = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        this.setState({
          loading: true,
        });
        this.refForm.current.validateFields(async (err, values) => {
          if (!err) {
            let formDataArray = this.getTableData(values);
            formDataArray = formDataArray.map((item, index) => {
              item.ccsId = dataScore[index].ccsId;
              item.studentId = dataScore[index].studentId;
              item.grade = parseInt(item.grade);
              return item;
            });
            await createClassCreditEnrollment(formDataArray);
            this.setState({
              dataScore: null,
            });
            message.success('Lưu thành công');
          }
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handeLock = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        this.setState({
          loading: true,
        });
        await updateClassCreditEnrollment({
          ccsId: dataScore[0].ccsId,
          isLocked: !dataScore[0].isLocked,
        });
        message.success(`${dataScore[0].isLocked ? 'Mở khóa' : 'Khóa'} thành công`);
        this.setState({
          dataScore: null,
        },
          () => this.handleChangeQueryParams(),
        );
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportPDF = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportPDF(paramSearch.cCSId);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = getQueryParams();
      await exportExcel(paramSearch.cCSId);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }


  render() {
    const { dataScore, reload, rowKey, loading } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title="Nhập điểm">
          <Row>
            <Col span={24}>
              <CreditClass />
            </Col>
          </Row>
          <Row>
            <Col span={24} style={{ textAlign: 'left' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.handleSave()} />
                <BtnLock
                  title={dataScore && dataScore.length && dataScore[0].isLocked ? 'Mở khóa' : 'Khóa'}
                  onClick={() => this.handeLock()}
                />
                <BtnExportPdf onClick={() => this.handleExportPDF()} />
                <BtnExport onClick={() => this.handleExportExcel()} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <CreditClassTable
                reload={reload}
                data={dataScore}
                ref={this.refForm}
                rowKey={rowKey}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScorePage />
  </Page>
);



