import { request } from 'utils';

const prefix = '/api/education';

export const getGradeNote = ({
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/GradeNote',
    method: 'GET',
    params: {page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });


